import React from 'react';
import './button-styles/button.css';
import './button-styles/button_light-purple.css';
import './button-styles/button_round-border.css';

const Button = (props)=>{
    const {name, borderModificator, colorModificator, action} = props;
    return(
        <a href="" onClick={action} className={"button" +" "+"button_"+borderModificator + " "+"button_"+colorModificator}>{name}</a>
    )
}

export default Button;