import React, {Component} from 'react';
import './form-field.css';

export default class FormField extends Component{

    cleanFieldError = (event)=>{
        if(event.target.classList.contains("form-field__error")){
            event.target.classList.remove("form-field__error");
            event.target.parentElement.children[1].textContent = "";
        }
    }

    render(){
        const {name, placeholder, type, castomClass} = this.props;
        return(
            <div className="form-field">
                <p className="form-field__name">{name}</p>
                <div className={"form-field__container "+castomClass}>
                    <input placeholder={placeholder} type={type} className="form-field__value" onClick={this.cleanFieldError}></input>
                    <p className="form-field__error-text"></p>
                </div>
            </div>
        )
    }
}
