let initialState = {
    wishes: []
}

const wishesReducer = (state=initialState, action)=> {
    switch(action.type){
        case "UPDATE_WISHES":
            return {
                ...state, wishes: action.value
            };
        case "ADD_WISH":
            let newState = state;
            newState.wishes.push(action.value);
            return {
                ...state,
                newState 
            }
        default: return state;
    }
};

export default wishesReducer;