import React, {Component} from 'react';
import FormField from '../FormField/FormField.js';
import Button from '../../Button/Button.js';
import WishListService from '../../../services/wishListService.js';
import wishes from '../../../actions/wishes.js';
import './create-wish-form.css';
import {connect} from "react-redux";

class CreateWishForm extends Component{

    WishListService = new WishListService();

    createWish = (event) =>{
        event.preventDefault();
        this.WishListService.createWish(
            JSON.stringify(
                {
                    name: document.querySelector(".wish-name").children[0].value,
                    description: document.querySelector(".wish-description").children[0].value,
                    link: "url",
                    instruction: document.querySelector(".wish-instruction").children[0].value,
                    price: +document.querySelector(".wish-price").children[0].value,
                    picture: document.querySelector(".wish-picture-link").children[0].value,
                    author: this.props.store.lastname + " " + this.props.store.firstname,
                    category: document.querySelector(".wish-category").children[0].value
                }
            )
        )
        this.props.dispatch(wishes.addWish(
            {
                name: document.querySelector(".wish-name").children[0].value,
                description: document.querySelector(".wish-description").children[0].value,
                link: "url",
                instruction: document.querySelector(".wish-instruction").children[0].value,
                price: +document.querySelector(".wish-price").children[0].value,
                picture: document.querySelector(".wish-picture-link").children[0].value,
                author: this.props.store.lastname + " " + this.props.store.firstname,
                category: document.querySelector(".wish-category").children[0].value
            }
        ));            
    }

    render(){
        return(
            <div className="create-wish-form">
                <FormField name="Наименование желания:" placeholder="" type="text" castomClass="wish-name" onClick={this.cleanFieldError}/>
                <FormField name="Ссылка на картинку:" placeholder="" type="text" castomClass="wish-picture-link" onClick={this.cleanFieldError}/>
                <FormField name="Описание желания:" placeholder="" type="text" castomClass="wish-description" onClick={this.cleanFieldError}/>
                <FormField name="Как можно осуществить:" placeholder="" type="text" castomClass="wish-instruction" onClick={this.cleanFieldError}/>
                <FormField name="Категория:" placeholder="" type="text" castomClass="wish-category" onClick={this.cleanFieldError}/>
                <FormField name="Примерная цена:" placeholder="" type="text" castomClass="wish-price" onClick={this.cleanFieldError}/>
                <artivle className="create-wish-form__manage-section">
                    <Button name="Создать" borderModificator="round-border" colorModificator="light-purple" action={this.createWish}/>
                    <Button name="Закрыть" borderModificator="round-border" colorModificator="light-purple" action=""/> 
                </artivle>
            </div>
        )
    }
}

export default connect(
    state => ({
        store: state.profileReducer
    })
)(CreateWishForm);