import React, {Component} from 'react';
import "./header.css";
import {connect} from "react-redux";

 class Header extends Component{
     render(){
         let header;
         if(this.props.store.firstname!=""){
            header =
            <header className="header header_authorized container">
                <div className="header__menu">
                    <a href="" className="header__menu-item">Список желаний</a>
                </div>
                <a href="" className="header__username">
                    <p className="header__firstname">{this.props.store.firstname}</p>
                    <p className="header__lastname">{this.props.store.lastname}</p>
                </a>
            </header>
         }else{
             header = 
             <header className="header header_not-authorized container">
                <p className="gretting-text">Привет!</p>
                <p className="gretting-text">Ты попал в мир, где исполняются желания! Ты готов?</p>
                <p className="gretting-text">Тогда поехали:)</p>
            </header>
         }
         return(
             header
            )
        }
    }

export default connect(
    state => ({
        store: state.profileReducer
    })
)(Header);