import React, {Component} from "react";
import './wish.css';

export default class Wish extends Component{
    render(){
        console.log(this.props);
        return(
            <div className="wish" style={{backgroundImage: "url('"+this.props.picture+ "')"}}>
                <article className="wishName">{this.props.name}</article>
                <article className="wishDescription">
                    <p>{this.props.description}</p>
                </article>
                <article className="wishAttributes">
                    <p className="wish-author">Автор: {this.props.author}</p>
                    <p className="wishCategory">Категория: {this.props.category}</p>
                    <p className="wish-price">Цена: {this.props.price}</p>
                    <a className="wish-link" href={this.props.link}>Ссылка</a>
                </article>
            </div>
        )
    }
}