import React, {Component} from 'react';
import FormField from '../FormField/FormField.js';
import Button from '../../Button/Button.js';
import WishListService from '../../../services/wishListService.js';
import checkMail from '../../../services/checkMailService.js';
import profile from '../../../actions/profile.js';
import {connect} from "react-redux";
import './authAndRegForm.css';

class AuthAndRegForm extends Component{

    WishListService = new WishListService();

    state={
        isRegistred: false
    }

    changerFrom = (event)=>{
        event.preventDefault();
        this.cleanFieldErrorAll();
        this.setState({
            isRegistred: this.state.isRegistred==false?true:false
        })
    }

    cleanFieldErrorAll = () =>{
        let fields = document.querySelectorAll(".form-field__error");
        for(let i = 0; i < fields.length; i++){
           fields[i].classList.remove("form-field__error");
           fields[i].parentElement.children[1].textContent = "";
        }
    }

    onRegistration = (event) =>{
        event.preventDefault();
        if(!checkMail(document.querySelector(".reg-login").children[0].value)){
            this.registrationResultProcessing("the specified email is not correct");
        }
        this.WishListService.onRegistration(
            document.querySelector(".reg-name").children[0].value,
            document.querySelector(".reg-lastname").children[0].value,
            document.querySelector(".reg-login").children[0].value,
            document.querySelector(".reg-password").children[0].value
        ).then(this.registrationResultProcessing)
    }

    onAuthorization = (event) =>{
        event.preventDefault();
        this.WishListService.onAuthorization(
            document.querySelector(".auth-login").children[0].value,
            document.querySelector(".auth-password").children[0].value
        ).then(this.authResultProcessing)
    }

    registrationResultProcessing = (value) =>{
        if(value=="the specified email is already in use"){
            document.querySelector(".reg-login").children[0].value = "";
            document.querySelector(".reg-login").children[1].textContent = "Электронная почта уже используется";
            document.querySelector(".reg-login").children[0].classList.add("form-field__error");
        }else if(value=="the specified email is not correct"){
            document.querySelector(".reg-login").children[0].value = "";
            document.querySelector(".reg-login").children[1].textContent = "Значение должно включать в себя комбинацию букв и цифр, значок @ и домен почтового клиента";
            document.querySelector(".reg-login").children[0].classList.add("form-field__error");
        }
    }

    authResultProcessing = (value) =>{
        if(value.firstname!=undefined){
            let user = {
                token:"",
                avatar:null,
                email: value.login,
                firstname: value.firstname,
                lastname: value.lastname,
                patronymic: null,
                gender: null,
            }
            this.props.dispatch(profile.changeProfileAttributes(user));
        }else if(value=="this account is not found"){
            document.querySelector(".auth-login").children[0].value = "";
            document.querySelector(".auth-login").children[1].textContent = "Аккаунт не найден";
            document.querySelector(".auth-login").children[0].classList.add("form-field__error");
        }else if(value=="this password is wrong"){
            document.querySelector(".auth-password").children[0].value = "";
            document.querySelector(".auth-password").children[1].textContent = "Неверный пароль";
            document.querySelector(".auth-password").children[0].classList.add("form-field__error");
        }
    }

    render(){
        let auth = <div className="authAndRegForm">
        <div className="headline-half form-half">
            <h3 className="form-headline">Войти</h3>
        </div>
        <div className="main-lalf form-half">
            <FormField name="Введите логин" placeholder="Укажите email" type="text" castomClass="auth-login" onClick={this.cleanFieldError}/>
            <FormField name="Введите пароль" placeholder="Секрет" type="password" castomClass="auth-password" onClick={this.cleanFieldError}/>
            <div className="form-actions">
                <Button name="Войти" borderModificator="round-border" colorModificator="light-purple" action={this.onAuthorization}/>
                <Button name="Зарегистрироваться" borderModificator="round-border" colorModificator="light-purple" action={this.changerFrom}/>
            </div>
        </div>
    </div>;

        let reg = <div className="authAndRegForm">
        <div className="headline-half form-half">
            <h3 className="form-headline">Регистрация</h3>
        </div>
        <div className="main-lalf form-half">
            <FormField name="Имя" placeholder="Имя" type="text" castomClass="reg-name" onClick={this.cleanFieldError}/>
            <FormField name="Фамилия" placeholder="Фамилия" type="text" castomClass="reg-lastname" onClick={this.cleanFieldError}/>
            <FormField name="Логин" placeholder="Логин" type="text" castomClass="reg-login" onClick={this.cleanFieldError}/>
            <FormField name="Пароль" placeholder="Пароль" type="password" castomClass="reg-password" onClick={this.cleanFieldError}/>
            <div className="form-actions">
                <Button name="Зарегистрироваться" borderModificator="round-border" colorModificator="light-purple" action={this.onRegistration}/>
            </div>
            <a className="authAndRegForm__to-login-link" href="#" onClick={this.changerFrom}>У меня уже есть аккаунт</a>
        </div>
    </div>;

        let form = this.state.isRegistred==true?reg:auth;        

        return(
            form            
        )
    }
}

export default connect(
    state => ({
        store: state
    })
)(AuthAndRegForm);