import React, {Component} from 'react';
import Button from "../Button/Button.js";
import CreateWishForm from '../Forms/CreateWishForm/CreateWishForm.js';
import Wish from '../Wish/Wish.js';
import wishes from '../../actions/wishes.js'
import {connect} from "react-redux";
import WishListService from '../../services/wishListService.js';
import './wish-list.css';

class WishList extends Component{

    WishListService = new WishListService();

    componentDidMount(){
        this.WishListService.getWish().then(
            this.updateWishes
        )
        console.log(this.props.wishStore.wishes);
    }

    updateWishes =(wishArr)=>{
        console.log(wishArr);
        this.props.dispatch(wishes.updateWishes(wishArr));
    }

    renderWishes =(arr)=>{
        console.log(arr);
        return(
            arr.map(item =>{
                return <Wish name={item.name} description={item.description} author={item.author} price={item.price} category={item.category} picture={item.picture}/>
            })
        )
    }

    render(){

        const wishes = this.renderWishes(this.props.wishStore.wishes);
        console.log(wishes);

        return(
            <div className="wish-list container">
                <Button name="Новое желание" borderModificator="round-border" colorModificator="light-purple" action=""/>
                <CreateWishForm/>
                {wishes}
            </div>
        )
    }
}

export default connect(
    state => ({
        store: state.profileReducer,
        wishStore: state.wishesReducer
    })
)(WishList);

