export default class WishListService{
    constructor(){
        this._apiBase = "http://localhost:8080/";
    }

    onRegistration = async (name, lastname, email, password)=>{
        let user = {
            "lastname": lastname,
            "firstname": name,
            "email": email,
            "password": password
        };
        let url = "reg";
        let json = JSON.stringify(user);
        const res = await fetch(`${this._apiBase}${url}`,{
            method: 'POST',
            headers: {
                'Content-Type': 'application/json;charset=utf-8'
            },
            body: json
        });
        if(!res.ok){
            if(res.status==409){
                return("the specified email is already in use");
            }else if(res.status==422){
                return("the specified email is not correct")
            }else{
                throw new Error(`Could not fetch ${url}; recived ${res.status}`);
            }
        }
        return await res;
    };

    onAuthorization = async (email, password)=>{
        let userAuth = {
            "login": email,
            "password": password
        }
        let url = "auth";
        let json = JSON.stringify(userAuth);
        const res = await fetch(`${this._apiBase}${url}`,{
            method: 'POST',
            headers: {
                'Content-Type': 'application/json;charset=utf-8'
            },
            body: json
        });
        if(!res.ok){
            if(res.status==404){
                return("this account is not found");
            }else if(res.status==409){
                return("this password is wrong")
            }else{
                throw new Error(`Could not fetch ${url}; recived ${res.status}`);
            }
        }
        return await res.json();
    }

    createWish = async (wish) =>{
        let url = "addwish";
        const res = await fetch(`${this._apiBase}${url}`,{
            method: 'POST',
            headers: {
                'Content-Type': 'application/json;charset=utf-8'
            },
            body: wish
        });
        if(!res.ok){
            throw new Error(`Could not fetch ${url}; recived ${res.status}`);
        }
        return await res.json();
    }

    getWish = async () =>{
        let url = "getwishes";
        const res = await fetch(`${this._apiBase}${url}`,{
            method: 'GET'
        });
        if(!res.ok){
            throw new Error(`Could not fetch ${url}; recived ${res.status}`);
        }
        return await res.json();
    }
}