import './App.css';
import React, {Component} from 'react';
import {BrowserRouter as Router, Route, Redirect} from 'react-router-dom';
import {connect} from 'react-redux';
import Header from './components/Header/Header.js';
import AuthAndRegForm from './components/Forms/AuthAndRegForm/AuthAndRegForm.js';
import WishList from "./components/WishList/WishList.js";
import WishListService from './services/wishListService';

class App extends Component {

  WishListService = new WishListService();

  render(){
    return (
      <div className="App container">
        <Router>
          <Header/>
          <Route path="/" render={(props)=>this.props.store.firstname!=""?(<Redirect to={{pathname:"wishlist"}}/>):<AuthAndRegForm/>} exact></Route>
          <Route path="/wishlist" render={(props)=><WishList/>}></Route>
        </Router>
      </div>
    );
  }
}

function mapStateToProps(state){
  return{
      store:state.profileReducer
  }
}

export default connect(
  mapStateToProps,
  dispatch => ({})
)(App);
